* {
	box-sizing: border-box;
	font-kerning: normal
}

html {
	font-size: 18px;
	line-height: 1.5;
	max-width: 68rem;
	margin: 0 auto;
	padding: 1rem;
	-webkit-font-smoothing: antialiased
}
main {
	min-height: calc(100vh - 20.5rem)
}

.contain {
	margin: 5rem 0
}

.contain .contain {
	margin: 0
}

.contain.sticky>div:first-child h1,
.contain.sticky>div:first-child h2,
.contain.sticky>div:first-child h3 {
	position: sticky;
	top: 0
}

.contain.sticky>header:first-child div {
	position: sticky;
	top: 1rem
}

@media(min-width:45rem) {
	.contain {
		display: grid;
		grid-template-columns: 1fr 2fr;
		grid-gap: 2rem;
		align-items: flex-start
	}
	.contain h2 {
		margin-top: 0
	}
	.contain p:nth-child(1),
	.contain ul:nth-child(1),
	.contain ol:nth-child(1),
	.contain div figure:nth-child(1) {
		margin-top: 0
	}
	.contain.baseline {
		align-items: baseline
	}

	.contain.sticky>div:first-child,
	.contain.sticky>header:first-child {
		height: 100%
	}
}

a,
a:visited {
	color: black;
	text-decoration: none;
}

nav ul {
	list-style: none;
	margin: 0;
	padding: 0;
	margin-bottom: 2.25rem;
	display: inline-block;
	font-size: 120%
}

nav ul li {
	display: inline-block;
	margin-right: 1rem
}

nav a {
	background: 0 0
}

nav>a {
	display: block;
	float: left;
	margin-right: 2rem
}

footer {
	padding: 2rem 0 0;
	margin-top: 1rem;
}

.blogpost {
	border-top: 1px solid black
}

.archiveblock {
	border-top: 1px solid black;
	border-bottom: 1px solid black;
	text-align: center;
	padding: 2rem 0;
}

.page404 {
	text-align: center;
}

.page404 h1{
	font-size: 4em;
}

@media (max-width: 45em){
    .custom div.section{
        padding-top:2em;
        padding-bottom:2em
    }
    .custom h1.emph{
        font-size:1.8em
    }
    .custom div.section.title h1{
        font-size:3.5em
    }
    .custom div.section.rainbowbreak h1{
        font-weight:200;
        font-size:2em
    }
}
@media (max-width: 600px){
    .custom{
        margin-left:-1.5rem !important;
        margin-right:-1.5rem !important
    }
}
@media (min-width: 600px) and (max-width: 64rem){
    .custom{
        margin-left:-2.35rem !important;
        margin-right:-2.35rem !important
    }
}

@media print {
	body {
		color: #000;
		background-color: #fff;
		font-size: 13px;
		max-width: 40em;
		margin-left: auto;
		margin-right: auto
	}
	a {
		color: #000
	}
	nav ul li {
		display: none
	}
	article.custom p,
	article.custom blockquote {
		color: #000!important
	}
}
